import { FILTER_ORG_UNIT } from '../../../actions/itemFilters.js';
export const getIframeSrc = (appDetails, item, itemFilters) => {
  let iframeSrc = "".concat(appDetails.launchUrl, "?dashboardItemId=").concat(item.id);

  if (itemFilters[FILTER_ORG_UNIT] && itemFilters[FILTER_ORG_UNIT].length) {
    const ouIds = itemFilters[FILTER_ORG_UNIT].map(_ref => {
      let {
        id,
        path
      } = _ref;
      return path ? path.split('/').slice(-1)[0] : id;
    });
    iframeSrc += "&userOrgUnit=".concat(ouIds.join(','));
  }

  return iframeSrc;
};